@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  background-image: url('https://cdn.pixabay.com/photo/2018/03/04/09/51/space-3197611_960_720.jpg');
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.button{
  box-shadow: 0 0 0.2rem 0 rgba(0, 0, 0, .4);   
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.4);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  height: 4em;
  border-radius: 3px;

  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Manrope', sans-serif;
  font-size: large;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: ease-out 0.2s;
}

p{
  font-family: 'Manrope', sans-serif;
}

h2{
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
}

#calculator{
  max-width: 36em;
}

#display-container{
  background-color: rgba(255, 255, 255, 1);
  border-radius: 4px 4px 0 0;
  height: 6em;
}

#display-container > p, #display-container > h2{
  margin: 0;
  float: right;
}

#history{
  float: left;
}

i{
  color: rgb(255, 95, 21);
}

#equals, #clear{
  background-color: rgb(255, 95, 21);
}

#equals > i, #clear {
  color: white;
}

.button:hover{
  background-color: rgba(255, 255, 255, 1);
  color: rgb(255, 95, 21);
}

#equals:hover,#clear:hover{
  background-color: rgb(255, 0, 0);
}
